#aboutStyle {
    background: -webkit-gradient(linear, left top, right bottom, from(rgba(225, 225, 225, .3)), to(rgba(204, 230, 255, 1)));
    background: -webkit-linear-gradient(top left, rgba(225, 225, 225, .3), rgba(204, 230, 255, 1));
    background: -o-linear-gradient(top left, rgba(225, 225, 225, .3), rgba(204, 230, 255, 1));
    background: linear-gradient(to bottom right, rgba(225, 225, 225, .3), rgba(204, 230, 255, 1));
    height: 75vh;
    margin-top: 100px;
    color: black;
    text-shadow: none;
}

.headerStyle {
    text-align: left;
    font-size: 20px;
    font-family: 'Amarante', cursive;
    margin-top: 80px;
    text-shadow: 2px 2px 2px #000000;
}

.aboutSections {
    margin-top: 5%;
}

.routingLinks {
    text-align: center;
    color: white;
    font-size: 15px;
}

.glyphicon {
    text-align: center;
}

#aboutGlyph {
    border-radius: 50%;
    margin-top: 100px;
    width: 25px;
    height: 25px;
    color: rgba(63, 127, 191, 1);
    text-shadow: 3px 3px 5px #000000;
    cursor: pointer;
    -webkit-box-shadow: 0 0 0 rgba(63, 127, 191, 0.4);
            box-shadow: 0 0 0 rgba(63, 127, 191, 0.4);
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
    text-align: center;
}

#aboutGlyph:hover {
    -webkit-animation: none;
            animation: none;
}

@-webkit-keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(63, 127, 191, 0.4);
    }
    70% {
        -webkit-box-shadow: 0 0 0 10px rgba(63, 127, 191, 0.2);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgba(63, 127, 191, 0.4);
    }
  }
  @keyframes pulse {
    0% {
      -webkit-box-shadow: 0 0 0 0 rgba(63, 127, 191, 0.4);
              box-shadow: 0 0 0 0 rgba(63, 127, 191, 0.4)
    }
    70% {
        -webkit-box-shadow: 0 0 0 10px rgba(63, 127, 191, 0.2);
                box-shadow: 0 0 0 10px rgba(63, 127, 191, 0.2);
    }
    100% {
        -webkit-box-shadow: 0 0 0 0 rgbargba(63, 127, 191, 0);
                box-shadow: 0 0 0 0 rgbargba(63, 127, 191, 0);
    }
  }

#quote {
    font-family: 'Courgette', cursive;
    margin-top: 20px;
    margin-bottom: 20px;
}







