#suggestionStyle {
    background: -webkit-gradient(linear, left bottom, right top, from(rgba(225, 225, 225, 0)), to(rgba(204, 230, 255, 1)));
    background: -webkit-linear-gradient(bottom left, rgba(225, 225, 225, 0), rgba(204, 230, 255, 1));
    background: -o-linear-gradient(bottom left, rgba(225, 225, 225, 0), rgba(204, 230, 255, 1));
    background: linear-gradient(to top right, rgba(225, 225, 225, 0), rgba(204, 230, 255, 1));
}

#suggestionHeader {
    text-align: left;
    font-size: 20px;
    font-family: 'Amarante', cursive;
}

#aokCardMargin {
    margin-top: 80px;
}


